// import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-router-dom'


export default function Navbar(props) {
	return (
		<>
			<nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
				<div className="container-fluid">
					<Link className="navbar-brand" to="/">{props.title}</Link>
					<button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
						<span className="navbar-toggler-icon"></span>
					</button>
					<div className="collapse navbar-collapse" id="navbarSupportedContent">
						<ul className="navbar-nav me-auto mb-2 mb-lg-0">
							<li className="nav-item">
								<Link className="nav-link" aria-current="page" to="/">Home</Link>
							</li>
							<li className="nav-item">
								<Link className="nav-link" to="/Aboutus">{props.about}</Link>
							</li>
						</ul>
						{/* <form className="d-flex">
							<input className="form-control me-2" type="search" placeholder="Search" aria-label="Search" />
							<button className="btn btn-primary" type="submit">Search</button>
						</form> */}
						<div className={`form-check form-switch text-${props.mode==='dark'?'light':'dark'}`}>
							<input className="form-check-input" type="checkbox" role="switch" id="flexSwitchCheckDefault" onClick={props.toogleMode}/>
							<label className ="form-check-label" htmlFor="flexSwitchCheckDefault">Enable Darks Mode</label>
						</div>
					</div>
				</div>
			</nav>

		</>
	)
}

Navbar.propTypes = {
	title: PropTypes.string.isRequired, // it tells that title is compulsary
	about: PropTypes.string // means about should be string data type
}

Navbar.defaultProps = {
	// title:"Please Set",
	about: "about"  //sets default values of about prop
}